import config from '../../../config';
import { SUCCESS } from '../../../store/callApi/actions';
import { timestampToDatetime } from '../../../utils';
import { useApi } from 'store/callApi';

interface Props {
  id: string;
  image: string;
  created_date: number;
  title: string;
  summary: string;
  source: string;
  setArticles: (article: any) => void;
  setArticlesParent: (article: any) => void;
  status: boolean;
  categories: { id: string; name: string }[];
  mini: boolean;
  categoryName?: string;
  setModal?: any;
  currentCategoryId?: string;
}

const Article: React.FC<Props> = (props) => {
  const { callApi } = useApi();

  const {
    id,
    image,
    created_date,
    title,
    summary,
    source,
    setArticles,
    setArticlesParent,
    status,
    categories,
    mini,
    categoryName,
    setModal,
    currentCategoryId,
  } = props;

  const changeFavorite = (e: any) => {
    console.log(currentCategoryId, categories);
    e.preventDefault();
    setArticles((articles: any) => {
      let newArticles = [...articles.data];
      // const index = newArticles.findIndex((item) => item.id === id);
      const newIndex = newArticles.findIndex((item) => item.id === id);
      newArticles[newIndex].status = !status;
      return { ...articles, data: newArticles };
    });
    setArticlesParent &&
      setArticlesParent((articles: any) => {
        let newArticles = [...articles.data];
        const newIndex = newArticles.findIndex((item) => item.id === id);
        if (newIndex === -1) return articles;
        newArticles[newIndex].status = !status;
        return { ...articles, data: newArticles };
      });

    callApi(
      {
        method: status ? 'delete' : 'post',
        api: config.rest.newsfeedFavoriteSetting(id),
        body: status
          ? undefined
          : {
              category_id: currentCategoryId || categories?.[0].id,
            },
        // loading: true,
      },
      (response) => {
        const status1 = response.status;
        if (status1 === SUCCESS) {
        }
      },
    );
  };

  const handleShowModal = (category: { id: string; name: string }, e: any) => {
    e.preventDefault();
    if (categoryName) return;
    setModal({ show: true, category });
  };

  const notInModal = !categoryName;

  return (
    <div className={`${notInModal ? 'col-lg-4 col-md-6' : 'col-xl-4 col-lg-6'} hb-card-item`}>
      {!mini ? (
        <a href={source} target="_blank">
          <div
            className="Article-thumbnail"
            style={{
              overflow: 'hidden',
              width: '100%',
              display: 'block',
              borderRadius: '32px 32px 0 0',
              height: 245,
              backgroundImage: `url("${image}")`,
              backgroundSize: 'cover',
              backgroundPosition: 'center',
            }}
          />
          <div
            style={{
              backgroundColor: 'white',
              padding: '20px',
              borderRadius: '0 0 30px 30px',
            }}
          >
            <div className="d-flex justify-content-between">
              <div className="hb-article-time">
                <i className="hb-icon-calendar-event" />
                <span className="hb-article-time-text">{timestampToDatetime(created_date)}</span>
              </div>
              <div className="hb-save" onClick={changeFavorite}>
                <i className={`${status ? 'hb-icon-heart-s' : 'hb-icon-heart-r'}`} />
              </div>
            </div>
            <div className="hb-card-title newsfeed hb-link">{title}</div>
            <p className="hb-card-summary">{summary}</p>
            <div className="d-flex" style={{ marginTop: 10 }}>
              {categories.slice(0, 3).map((item) => (
                <div key={item.id} className="hb-card-keyword" onClick={(e) => handleShowModal(item, e)}>
                  {item.name}
                </div>
              ))}
            </div>
          </div>
        </a>
      ) : (
        <a href={source} target="_blank">
          <div
            style={{
              backgroundColor: 'white',
              padding: 15,
              borderRadius: 32,
              height: 124,
              display: 'flex',
              alignItems: 'center',
            }}
          >
            <div>
              <div
                style={{
                  overflow: 'hidden',
                  display: 'block',
                  borderRadius: 20,
                  width: 94,
                  height: 94,
                  backgroundImage: `url("${image}")`,
                  backgroundSize: 'cover',
                  backgroundPosition: 'center',
                }}
              />
            </div>
            <div
              style={{
                marginLeft: 10,
                width: 'calc(100% - 130px)',
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
                flex: 1,
              }}
            >
              <div className="hb-card-title-mini hb-link">{title}</div>
              <div className="d-flex justify-content-between">
                <div className="d-flex" style={{ width: 'calc(100% - 40px)' }}>
                  {categories.length > 0 && (
                    <div
                      className={`hb-card-keyword${categoryName ? ' disabled' : ''}`}
                      onClick={(e) => handleShowModal(categories[0], e)}
                    >
                      {categoryName || categories[0].name}
                    </div>
                  )}
                </div>
                <div className="hb-save" onClick={changeFavorite}>
                  <i className={`${status ? 'hb-icon-heart-s' : 'hb-icon-heart-r'}`} />
                </div>
              </div>
            </div>
          </div>
        </a>
      )}
    </div>
  );
};

export default Article;
