import React, { useEffect, useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import config from '../../../config';
import { SUCCESS } from '../../../store/callApi/actions';
import Article from './Article';
import { useSelector } from 'react-redux';
import Frame from '../mobility/Frame';
import { loadingSmall, ls } from '../../../extensions';
import { HBButtonSmall, HBScrollMenuCategories, HBButton } from '../../../hbBaseClass';
import ModalArticles from './ModalArticles';
import './Newsfeed.scss';
import { concatData, getNextPage, getPageSize } from 'utils';
import { useApi } from 'store/callApi';
import { useMemo } from 'react';
import Saly24 from 'assets/images/Saly-24.png';

interface Props {
  isFavorite: boolean;
}

const breakPointTablet = '(max-width: 991px)';

const ViewArticles: React.FC<Props> = (props) => {
  const { t } = useTranslation();
  const { isFavorite }: any = props;
  console.log(isFavorite)

  const { callApi } = useApi();

  const history = useHistory();

  const location = useLocation();
  const query = new URLSearchParams(location.search);
  const topic = query.get('topic') || 'all';

  const [topics, setTopics] = useState<any>({ data: [], loading: true });
  const [articles, setArticles] = useState<any>({ data: [], loading: true, full: false, page: 0 });
  const [modal, setModal] = useState({ show: false, category: { id: '', name: '' } });
  const [isTablet, setIsTablet] = useState(window.matchMedia(breakPointTablet).matches);

  const email = useSelector((state: any) => state.auth.email);

  // get favorite articles
  const { listArticles, favSubArticles } = useMemo(() => {
    const articleLength = isTablet ? 2 : 3;
    const { data = [] } = articles || {};
    const listArticles = isFavorite ? data.slice(0, articleLength) : data;

    return {
      listArticles,
      favSubArticles: isFavorite ? data.slice(articleLength) : [],
    };
  }, [isFavorite, articles, isTablet]);

  const getAllArticles = (topic: string, page: number) => {
    const pageSize = getPageSize(page);

    callApi(
      {
        method: 'get',
        api:
          topic === 'all'
            ? (isFavorite ? config.rest.getNewsfeedFavoriteArticles : config.rest.getNewsfeedArticles)(pageSize, page)
            : (isFavorite ? config.rest.getNewsfeedFavoriteArticlesById : config.rest.getNewsfeedArticlesById)(
                topic,
                pageSize,
                page,
              ),
        loading: page === 1,
      },
      (response) => {
        const { data, status } = response;
        if (status === SUCCESS) {
          setArticles((articles: any) => {
            const newData = concatData(page, articles.data, data.articles);
            const full = newData.length >= data.total;
            return {
              data: newData,
              full,
              loading: false,
              page: page,
            };
          });
        }
      },
    );
  };

  const getAllTopics = () => {
    callApi(
      {
        method: 'get',
        api: isFavorite ? config.rest.getNewsfeedFavoriteCategories() : config.rest.getNewsfeedSeletectedCategories(),
        loading: true,
      },
      (response) => {
        const { data, status } = response;
        if (status === SUCCESS) {
          if (data) {
            setTopics((topics: any) => {
              const newTopics = { ...topics, loading: false };
              newTopics.data = [{ id: 'all', name: 'Alle Themen', number_of_record: data?.total }, ...data.categories];
              return newTopics;
            });
            if (topic !== 'all' && data.categories.findIndex((item: any) => item.id === topic) === -1) goto('all');
            else getAllArticles(topic, 1);
          } else setArticles({ ...articles, loading: false });
        } else {
          setTopics({ ...topics, loading: false });
        }
      },
    );
  };

  /**
   * Handle check tablet
   */
  const handleCheckTablet = () => {
    setIsTablet(window.matchMedia(breakPointTablet).matches);
  };

  useEffect(() => {
    window.addEventListener('resize', handleCheckTablet);

    return () => {
      window.removeEventListener('resize', handleCheckTablet);
    };
  }, []);

  useEffect(() => {
    const screen = ls.get(`screen-${email}`);
    if (!screen) {
      history.push('/newsfeed/intro');
      return;
    }
    if (!isFavorite && screen === '/newsfeed/select-category') {
      history.push('/newsfeed/select-category');
      return;
    }
    if (topics.data.length === 0) getAllTopics();
    else {
      setArticles({ ...articles, loading: true, full: false, page: 0 });
      getAllArticles(topic, 1);
      window.scrollTo(0, 0);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [topic, location]);

  const loadMoreArticles = () => {
    if (articles.loading || articles.full) return;
    setArticles({ ...articles, loading: true });
    getAllArticles(topic, getNextPage(articles.page));
  };

  const goto = (topic: string) => {
    if (topic === 'all') history.push(isFavorite ? '/newsfeed/favorite' : '/newsfeed');
    else history.push(`/newsfeed/${isFavorite ? 'favorite' : ''}?topic=${topic}`);
  };

  const nextTopicIndex = topics.data.findIndex((item: any) => item.id === topic);
  const nextTopic = topics.data[nextTopicIndex === topics.data.length - 1 ? 0 : nextTopicIndex + 1];

  return (
    <>
      <ModalArticles
        setArticlesParent={setArticles}
        {...modal}
        handleClose={() => setModal({ ...modal, show: false })}
      />
      <div className="hb-mx-20">
        <div className="d-flex justify-content-between hb-my-28 Newsfeed-wrapperBtn">
          {isFavorite ? (
            <HBButtonSmall onClick={() => history.push('/newsfeed')}>
              <i className="hb-icon-arrow-left hb-icon-md" /> Zurück
            </HBButtonSmall>
          ) : (
            <>
              <HBButtonSmall onClick={() => history.push('/newsfeed/select-category')}>Themenauswahl</HBButtonSmall>
              <HBButtonSmall onClick={() => history.push('/newsfeed/favorite')}>
                <i className="hb-icon-heart-r hb-icon-md" />
                Gespeicherte
              </HBButtonSmall>
            </>
          )}
        </div>
      </div>
      <h1 className="Newsfeed-title">{isFavorite ? 'Gespeicherte Artikel' : 'Pflegemagazin'}</h1>
      <HBScrollMenuCategories
        data={topics.data.filter((item: any) => item.id !== '1')}
        topic={topic}
        goto={goto}
        hideNumber={isFavorite}
      />
      {!articles.loading && articles.data.length === 0 && (
        <div className="hb-no-content">
          <div className="hb-no-content__image">
            <div className="image-content-wrap">
              <h2 className="image-content-title">Keine Gespeicherten Artikel</h2>
              <h4 className="image-content">Um Artikel zu speichern, tippen</h4>{' '}
              <h4 className="image-content-below">Sie auf das Herz beim Artikel.</h4>
            </div>
            <div style={{ backgroundImage: `url("${Saly24}")` }} className="image-saly" />
          </div>
        </div>
      )}
      {articles.data.length > 0 &&
        (!isFavorite ? (
          <div className={`hb-card Newsfeed-content ${!isFavorite && 'basic'}`}>
            <div className="row">
              {listArticles.map((article: any, index: number) => (
                <Article
                  setArticles={setArticles}
                  {...article}
                  index={index}
                  key={index}
                  setModal={setModal}
                  currentCategoryId={topic !== 'all' ? topic : undefined}
                />
              ))}
            </div>
          </div>
        ) : (
          <div className={`hb-card Newsfeed-content ${isFavorite && 'favorite'}`}>
            <div className="hb-card-header">Kürzlich hinzugefügt</div>
            <div className="row">
              {listArticles.map((article: any, index: number) => (
                <Article setArticles={setArticles} {...article} index={index} key={index} setModal={setModal} />
              ))}
            </div>
            {!!favSubArticles?.length && (
              <>
                <div className="hb-card-header old">Ältere Beiträge</div>
                <div className="row subFavorite">
                  {favSubArticles.map((article: any, index: number) => (
                    <Article
                      setArticles={setArticles}
                      {...article}
                      index={index}
                      key={index}
                      mini={true}
                      setModal={setModal}
                    />
                  ))}
                </div>
              </>
            )}
          </div>
        ))}
      {articles.page !== 0 && articles.loading && <Frame size="large" render={loadingSmall} />}
      <div className="text-center mb-2 Newsfeed-actions">
        {!articles.loading && topics.data.length > 0 && (
          <HBButton
            onClick={() => (articles.full ? goto(nextTopic.id) : loadMoreArticles())}
            style={{ padding: '10px 20px' }}
          >
            {articles.full ? `Zum nächsten Thema: ${nextTopic.name}` : t('loadMore')}
          </HBButton>
        )}
      </div>
    </>
  );
};

export default ViewArticles;
